:root {
    --primary-color-hue: 252;
    --dark-color-lightness: 17%;
    --light-color-lightness: 95%;
    --white-color-lightness: 100%;

    --color-white: hsl(252, 30%, var(--white-color-lightness));
    --color-light: hsl(252, 30%, var(--light-color-lightness));
    --color-gray: hsl(var(--primary-color-hue), 15%, 65%);
    --color-primary: hsl(var(--primary-color-hue), 75%, 60%);
    --color-primary-light: hsl(var(--primary-color-hue), 45%, 30%);
    --color-secondary: hsl(252, 100%, 90%);
    --color-success: hsl(120, 95%, 65%);
    --color-danger: hsl(0, 95%, 65%);
    --color-dark: hsl(252, 30%, var(--dark-color-lightness));
    --color-black: hsl(252, 30%, 10%);
    --color-border: rgba(204, 204, 204, 0.337);
    --white: aliceblue;

    --border-radius: 2rem;
    --card-border-radius: 1rem;
    --btn-padding: 0.6rem 2rem;
    --search-padding: 0.6rem 1.5rem;
    --card-padding: 1rem;

    --color-bg-hover: rgba(147, 144, 144, 0.144);

    --border-color: rgb(67, 23, 239);

    --sticky-top-left: 10%;
    --sticky-top-right: 10%;

    --hue: 223;
    --bg: hsl(var(--hue), 90%, 95%);
    --fg: hsl(var(--hue), 90%, 5%);
    --trans-dur: 0.3s;
}

/* CUSTOME SCROLLBAR */

/* For Chrome, Safari and Opera */
.scrollbar::-webkit-scrollbar {
    width: 5px;
}

/* For Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: #ccc #fff;
}

/* Custom styling of scrollbar thumb */
.scrollbar::-webkit-scrollbar-thumb {
    background-color: var(--color-primary);
    border-radius: 5px;
}

/* Custom styling of scrollbar track */
.scrollbar::-webkit-scrollbar-track {
    background-color: var(--color-primary-light);
}
